.hero {
  position: relative;
}

.hero-bg {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
}
.hero-bg svg {
  max-width: 100%;
  height: 100%;
}

.hero-container {
  padding: 12px 12px 20px 12px;
}
@media (min-width: 768px) {
  .hero-container {
    padding: 70px 0 100px 0;
    max-width: 710px;
  }
}
@media (min-width: 992px) {
  .hero-container {
    padding: 100px 0 150px 0;
    max-width: 1080px;
  }
}
@media (min-width: 992px) {
  .hero-container .hero__title {
    margin-bottom: 13px;
  }
}
@media (min-width: 768px) {
  .hero-container .hero__description {
    margin-bottom: 25px;
  }
}
@media (min-width: 992px) {
  .hero-container .hero__description {
    margin-bottom: 35px;
  }
}

@media (min-width: 768px) {
  .swiper-slide {
    display: grid;
    grid-template-columns: repeat(11, 1fr);
  }
}

.swiper-pagination {
  position: relative;
  top: 10px;
}

.swiper-pagination-bullet-active {
  background-color: var(--clr-primary);
}

.hero__title {
  font-size: 36px;
  line-height: 46px;
}
@media (min-width: 768px) {
  .hero__title {
    font-size: 38px;
    grid-column: 1/7;
    grid-row: 2/3;
  }
}
@media (min-width: 992px) {
  .hero__title {
    font-size: 59px;
    line-height: 71px;
  }
}

.hero__img--wrapper {
  position: relative;
  margin-top: -95px;
  z-index: -1;
  filter: drop-shadow(0px 3px 16px rgba(0, 0, 0, 0.160784));
}
@media (min-width: 768px) {
  .hero__img--wrapper {
    margin-top: 0;
    grid-column: 5/12;
    grid-row: 1/6;
  }
}

.hero__description {
  margin: 10px 0 16px 0;
}
@media (min-width: 768px) {
  .hero__description {
    grid-column: 1/6;
    grid-row: 3/4;
  }
}

@media (min-width: 768px) {
  .hero__buttons {
    grid-column: 1/6;
    grid-row: 4/5;
  }
}

.hero-button {
  font-size: 13px;
  line-height: 15px;
  font-weight: 900;
  width: 140px;
  padding: 10px 0;
  letter-spacing: 1.3px;
  transition: color 200ms ease-in-out, background-color 300ms ease-in-out;
}
@media (min-width: 768px) {
  .hero-button {
    width: 130px;
  }
}
@media (min-width: 992px) {
  .hero-button {
    font-size: 21px;
    line-height: 25px;
    padding: 20px 0;
    width: 200px;
  }
}

a.button-1:hover {
  background-color: white !important;
  color: var(--clr-primary) !important;
}

a.button-2:hover {
  background-color: var(--clr-primary) !important;
  color: white !important;
}

.big-section {
  position: relative;
}
.big-section::after {
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  position: absolute;
  background-color: #f7f7f7;
  z-index: -1;
  clip-path: polygon(0 0, 100% 0, 100% 25%, 0% 32%);
}
@media (min-width: 768px) {
  .big-section::after {
    clip-path: polygon(0 0, 100% 0, 100% 25%, 0% 34%);
  }
}
@media (min-width: 992px) {
  .big-section::after {
    clip-path: polygon(0 0, 100% 0, 100% 25%, 0% 37%);
  }
}
.big-section::before {
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  position: absolute;
  background-color: #caeac7;
  background-image: url("../assets/img/slash_white.png");
  background-size: 100%;
  background-position-y: 70%;
  background-repeat: no-repeat;
  clip-path: polygon(0 32%, 100% 25%, 100% 62%, 0% 69%);
  z-index: -1;
}
@media (min-width: 768px) {
  .big-section::before {
    position: absolute;
    clip-path: polygon(0 34%, 100% 25%, 100% 65%, 0% 74%);
    background-position-y: 100%;
    background-position-x: right;
    background-size: 70%;
  }
}
@media (min-width: 992px) {
  .big-section::before {
    background-size: 50%;
    clip-path: polygon(0 37%, 100% 25%, 100% 68%, 0% 80%);
  }
}

.big-section-bg {
  width: 100%;
  position: absolute;
  top: 25%;
  height: 25%;
}
@media (min-width: 768px) {
  .big-section-bg {
    height: 35%;
    top: 35%;
  }
}

.reach {
  background-image: url("../assets/img/slash_black.svg");
  background-blend-mode: normal;
  position: relative;
  background-size: cover;
  background-repeat: repeat-y;
  background-position-y: -100px;
}
@media (min-width: 768px) {
  .reach {
    background-repeat: no-repeat;
    background-size: 50%;
    background-position-y: -50px;
    background-position-x: -30px;
  }
}
@media (min-width: 992px) {
  .reach {
    background-position-y: -100px;
  }
}

.reach-container {
  position: relative;
  padding: 20px 8px;
}
@media (min-width: 768px) {
  .reach-container {
    padding: 85px 0 55px 0;
